body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color: #777;
}

html,
body,
#root {
  height: 100%;
}

.big-text {
  text-align: center;
  padding: 2rem;
  font-size: 1.4rem;
  color: #777;
}

.border-error {
  border: 1px solid red !important;
}

.view-container {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

button.pure-menu-link {
  border: none;
}

button.pure-menu-link:focus {
  outline: none;
}
.main-menu {
  padding: 1rem;
  border-bottom: 1px solid #cbcbcb;
}

.menu-selected {
  background-color: #eee;
}

.pure-menu-item {
  margin: 0 0.5rem;
}

.pure-menu-link {
  cursor: pointer;
}

.backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 5rem;
  z-index: 2;
}

.modal {
  background-color: #fff;
  border-radius: 5;
  max-width: 30rem;
  margin: 0 auto;
  padding: 0 2rem 2rem;
}

.modal .header {
  position: relative;
  border-bottom: 1px solid #ddd;
  margin-bottom: 1rem;
}

.modal .header .icono-cross {
  position: absolute;
  top: 0;
  right: 1rem;
}

.modal .title {
  font-weight: bold;
  padding: 1.5rem 1rem 1rem 1rem;
}

.modal .footer {
  border-top: 1px solid #ddd;
  margin-top: 1rem;
  padding-top: 1rem;
  text-align: center;
}

.modal-code-editor textarea {
  width: 100%;
  height: 10rem;
}

.modal-data-element-selector select {
  width: 100%;
}

.main-container {
  text-align: center;
}

.main-container .pure-button {
  margin-left: 0.5rem;
}

.status-success strong {
  color: green;
}

.status-failed strong {
  color: red;
}

.other-settings-container {
  width: 45rem;
  margin: 2rem auto;
}

.other-settings-container .pure-form-aligned .pure-control-group {
  margin-right: 4rem;
}

.property-settings-container {
  width: 45rem;
  margin: 2rem auto;
}

.property-settings-container .pure-form-aligned .pure-control-group {
  margin-right: 4rem;
}

.list-container {
  width: 95%;
  margin: 1rem auto;
}

.list-table {
  margin: auto;
  width: 100%;
}

.list-item-name {
  text-align: left;
}

.list-item-actions {
  text-align: center;
  white-space: nowrap;
  width: 10%;
}

.create-new-button {
  margin-top: 1rem;
}

.container {
  margin: 1rem;
}

.button-container {
  border-top: 1px solid #e5e5e5;
  padding-top: 1rem;
}

.component-group {
  margin: 1rem 0;
}

.add-button {
  clear: both;
}

.rule-components-list:after {
  content: '';
  display: table;
  clear: both;
  padding-bottom: 1rem;
}

.rule-component-card {
  border: 1px solid #e3e3e3;
  float: left;
  padding: 1rem;
  margin: 1rem 1rem 0 0;
  width: 10rem;
  background: #f5f5f5;
  text-align: center;
  text-overflow: ellipsis;
  overflow: hidden;
}

.icono-file,
.icono-cross {
  color: #0078e7;
  margin-top: 1rem;
}

button.icono-cross {
  background: none;
  border: none;
}

button.icono-cross:focus {
  outline: none;
}

.card-extension-display-name,
.card-display-name {
  padding: 0.5rem 0 0 0;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.icono-cross {
  cursor: pointer;
}

.component-edit-container, .component-edit-container .pure-u-1-4 {
  height: 100%;
}

.component-edit-sidebar {
  border-right: 1px solid #cbcbcb;
  height: 100%;
  padding: 0 2rem;
  overflow: auto;
}

.component-edit-sidebar input[type="text"],
.component-edit-sidebar select {
  width: 100%;
}

.button-container {
  padding-bottom: 1rem;
}

.component-iframe,
.component-iframe iframe {
  width: 100%;
  height: 100%;
  border: 0;
}

.backdrop-only {
  text-align: center;
  font-size: 1.5rem;
  background: rgba(0, 0, 0, 0.7);
  color: white;
}

.lds-ring {
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
    top: 1.5rem;
    left: -1.5rem;
  }
  .lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 51px;
    height: 51px;
    margin: 6px;
    border: 6px solid #fff;
    border-radius: 50%;
    -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
            animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: #fff transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
    -webkit-animation-delay: -0.45s;
            animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
    -webkit-animation-delay: -0.3s;
            animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
    -webkit-animation-delay: -0.15s;
            animation-delay: -0.15s;
  }
  @-webkit-keyframes lds-ring {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @keyframes lds-ring {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
